<template>
<div class="timeline-container" id="timeline-1" ref="timeline_1">
  <div class="timeline-header">
    <h2 class="timeline-header__title">{{ title[$i18n.locale] }}</h2>
    <h3 class="timeline-header__subtitle">{{ position[$i18n.locale] }}</h3>
  </div>
  <div class="timeline">
    <div
      class="timeline-item"
      v-for="(event, index) in events"
      :key="index"
      :data-text="event.date"
      >
      <div class="timeline__content">
        <img class="timeline__img" :src="event.background" />
        <h2 class="timeline__content-title">{{ event.title[$i18n.locale] }}</h2>
        <p class="timeline__content-desc">{{ event.body[$i18n.locale] }}</p>
      </div>
    </div>
  </div>
</div>

</template>

<script>
import '@/assets/styles/timeline.css';

export default {
  props: ['events', 'title', 'position'],
  data() {
    return {
    };
  },
  mounted() {
    if(this.events[0]) {
      if(this.events[0].background) {
        this.$refs.timeline_1.style.backgroundImage = `linear-gradient(black, black), url(${this.events[0].background})`;
      }
      window.addEventListener('scroll', this.handleScroll);
    }
  },
  destroyed() {
    window.removeEventListener('scroll', this.handleScroll);
  },
  methods: {
    handleScroll() {
      let max;
      let min;
      const pos = window.scrollY;
      const bodyRect = document.body.getBoundingClientRect();
      const el = this.$refs.timeline_1;

      el.querySelector('.timeline-item').classList.add('timeline-item--active');

      Array.from(el.getElementsByClassName('timeline-item')).forEach((e) => {
        const rect = e.getBoundingClientRect();
        const offset = rect.top - bodyRect.top;
        const height = rect.height;
        min = offset;
        max = height + offset;

        if(window.innerHeight + window.scrollY >= bodyRect.height - 150) {
          Array.from(el.getElementsByClassName('timeline-item')).forEach(item => item.classList.remove('timeline-item--active'));
          const img = this.events[this.events.length - 1].background;
          if(img) el.style.backgroundImage = `linear-gradient(black, black), url(${img})`;
          el.getElementsByClassName('timeline-item')[this.events.length - 1].classList.add('timeline-item--active');
        } else if(pos <= max - 150 && pos >= min - 300) {
          Array.from(el.getElementsByClassName('timeline-item')).forEach(item => item.classList.remove('timeline-item--active'));
          e.classList.add('timeline-item--active');

          const img = e.querySelector('.timeline__img').getAttribute('src');
          if(img) el.style.backgroundImage = `linear-gradient(black, black), url(${img})`;
        }
      });
    },
  },
};

</script>



