<template>
<div class='flex'>
  <div class='details hide' ref="langueage" @mouseleave="languageHover(false)">
    <div class="summary" @mouseover="languageHover(true)" >
      <flag :iso="locales[$i18n.locale].flag" :squared="false" />
    </div>
    <div class='opt-wrapper'>
      <button
        @click="switchLanguage"
        type="button"
        v-for="(data, id) in locales"
        :key="id"
        :data-lang='id'
        class='opt'
        :class='{ selected: id === $i18n.locale }'
      >
        <flag :iso="data.flag" :squared="false" />
      </button>
    </div>
  </div>
</div>
</template>

<script>

export default {
  data() {
    return {
      locales: {
        en: { title: 'English', flag: 'gb' },
        by: { title: 'Беларусский', flag: 'by' },
        ru: { title: 'Русский', flag: 'ru' },
      },
    };
  },
  created() {
    const langueage = localStorage.getItem('langueage');
    if (langueage) this.$i18n.locale = langueage;
    else this.$i18n.locale = 'ru';
  },
  methods: {
    switchLanguage(e) {
      let button = e.target;
      if(button.nodeName !== 'button') button = button.closest('button');
      const btns = document.querySelectorAll('button.opt');
      Array.from(btns).forEach(btn => btn.classList.remove('selected'));

      button.classList.add('selected');
      localStorage.setItem('langueage', button.dataset.lang);
      this.$i18n.locale = button.dataset.lang;
      this.$refs.langueage.classList.add('hide');
    },
    languageHover(bool) {
      const langClassList = this.$refs.langueage.classList;
      if (bool) langClassList.remove('hide');
      else langClassList.add('hide');
    },
  },
};
</script>

<style>
.hide .opt-wrapper {
  visibility: hidden;
}

* {
  outline: none;
}
.summary {
  padding: 0 10px;
  cursor: pointer;
  font-size: 14px;
  text-align-last: left;
  text-transform: capitalize;
}
.details {
  display: block;
  position: relative;
}
button.selected {
  background: silver;
}
button.selected:hover {
  background: silver;
}
button {
  text-transform: capitalize;
  display: inline-block;
  width: 100%;
  background: none;
  border: 0;
  padding: 10px;
  cursor: pointer;
  border-bottom: 1px solid white;
  text-align: left;
  font-size: 14px;
  color: #000;
}
button:hover {
  background: #bbb;
  transition: 0.2s;
}
.details .summary::-webkit-details-marker {
  display: none !important;
}
.opt-wrapper {
  background: #eee;
  position: absolute;
  z-index: 100000000;
}
.flex {
  display: flex;
  align-items: start;
}
select {
  margin-top: 10px;
  display: inline-block;
}
</style>



