import Vue from 'vue';
import Router from 'vue-router';

import Home from '@/view/Home';
import Add from '@/view/Add';

import People from '@/view/People';
import peoples from '@/data/peoples';

Vue.use(Router);

const peoplesPaths = [];
Object.keys(peoples).forEach((path) => {
  peoplesPaths.push({
    path: `/${path}`,
    component: People,
  });
});

export default new Router({
  mode: 'hash',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home,
    },
    {
      path: '/add',
      name: 'Add',
      component: Add,
    },
    ...peoplesPaths,
  ],
});



