<template>
  <section id="peoples" name="peoples">
    <h2>{{ title[$i18n.locale] }}</h2>
    <router-link v-for="(people, path) in peoples" :to="'/' + path" v-bind:key="path">
      {{ people.title[$i18n.locale] }}
    </router-link>
  </section>
</template>

<script>
import peoples from '@/data/peoples';

export default {
  data() {
    return {
      peoples,
      title: {
        en: 'Peoples',
        ru: 'Люди',
        by: 'Людзі',
      },
    };
  },
};
</script>

<style scoped>
a {
  margin-right: 5px;
}
a:visited {
  color: inherit;
}
</style>



