<template>
<div class="form-style-8">
  <h2>{{ labels.pageTitle[$i18n.locale] }}</h2>
  <form id="events">
    <input
      reqired
      v-model="initials.ru"
      type="text"
      name="title"
      :placeholder="labels.initials[$i18n.locale]"
    />
    <input
      reqired
      v-model="position.ru"
      type="text"
      name="title"
      :placeholder="labels.position[$i18n.locale]"
    />
    <div class="event" v-for="(event, index) in events" :key="index">
      <input
        reqired
        v-model="events[index].title.ru"
        type="text"
        name="title"
        :placeholder="labels.title[$i18n.locale]"
      />
      <input
        reqired
        v-model="events[index].date"
        type="text"
        name="date"
        :placeholder="labels.date[$i18n.locale]"
      />
      <textarea
        reqired
        v-model="events[index].description.ru"
        :placeholder="labels.description[$i18n.locale]"
        @keydown="adjustTextarea"
      ></textarea>
      <input
        reqired
        v-model="events[index].image"
        type="text"
        name="image"
        :placeholder="labels.image[$i18n.locale]"
      />
      <input
        reqired
        v-model="events[index].source"
        type="text"
        name="source"
        :placeholder="labels.source[$i18n.locale]"
      />
      <input
        type="button"
        :value="labels.delete[$i18n.locale]"
        @click="onDelete(index)"
        class="alert"
      />
    </div>
    <input type="button" :value="labels.add[$i18n.locale]" @click="onAdd" />
    <input type="button" :value="labels.save[$i18n.locale]" @click="onSave" />
  </form>
  <div class="alert" :class='{ blink: alert}'>{{ alertMessage[$i18n.locale] }}</div>

</div>

</template>

<script>
import autosize from 'autosize';

const blankEvent = {
  date: '',
  title: { ru: '' },
  description: { ru: '' },
  image: '',
  source: '',
};

export default {
  data() {
    return {
      alert: false,
      alertMessage: {
        en: 'alert',
        ru: 'внимание',
        by: 'увага',
      },
      labels: {
        pageTitle: {
          en: 'Add people',
          ru: 'Добавить человека',
          by: 'Дадаць чалавека',
        },
        pageDescriptoon: {
          en: 'Throw the downloaded file off the telegram bot:',
          ru: 'Скачанный файл скиньте telegram боту:',
          by: 'Запампаваны файл Скіньце telegram робату:',
        },
        title: {
          en: 'Title',
          ru: 'Заголовок',
          by: 'Загаловак',
        },
        description: {
          en: 'Description',
          ru: 'Описание',
          by: 'Апісанне',
        },
        add: {
          en: 'Add event',
          ru: 'Добавить событие',
          by: 'Дадаць падзею',
        },
        delete: {
          en: 'Delete',
          ru: 'Удалить',
          by: 'Выдаліць',
        },
        save: {
          en: 'Save',
          ru: 'Сохранить',
          by: 'Захаваць',
        },
        position: {
          en: 'Position',
          ru: 'Должность',
          by: 'Пасада',
        },
        date: {
          en: 'Date',
          ru: 'Дата',
          by: 'Дата',
        },
        event: {
          en: 'Event',
          ru: 'Событие',
          by: 'Падзея',
        },
        initials: {
          en: 'Full name',
          ru: 'ФИО',
          by: 'Прозвішча',
        },
        image: {
          en: 'Image(optional)',
          ru: 'Картинка(необязательно)',
          by: 'Карцінка (неабавязкова)',
        },
        source: {
          en: 'Source(optional)',
          ru: 'Источник(необязательно)',
          by: 'Крыніца(неабавязкова)',
        },
      },
      initials: {
        ru: '',
      },
      position: {
        ru: '',
      },
      events: [
        {
          date: '',
          title: { ru: '' },
          description: { ru: '' },
          image: '',
          source: '',
        },
      ],
    };
  },
  methods: {
    onAdd() { this.events.push(blankEvent); },
    onSave() {
      this.formValidate();
      if(!this.alert) {
        const { events, initials, position } = this;
        const url = 'https://getsimpleform.com/messages?form_api_token=5a6bcd18be492b08833678c8d6c43baa';
        const formData = new FormData();
        formData.append('initials', JSON.stringify(initials));
        formData.append('position', JSON.stringify(position));
        formData.append('events', JSON.stringify(events));

        fetch(url, {
          method: 'POST',
          mode: 'no-cors',
          body: formData,
          headers: { 'Content-Type': 'application/json;charset=utf-8' },
        }).then(response => response).then(() => {
          this.events = [blankEvent];
          this.initials = { ru: '' };
          this.position = { ru: '' };
        }).catch(() => {
          this.errorMsg({
            en: 'An error occured, please try sending again',
            ru: 'Произошла ошибка, попробуйте отправить ещё раз',
            by: 'Адбылася памылка, паспрабуйце адправіць яшчэ раз',
          });
        });
      }
    },
    onDelete(i) {
      this.events.splice(i, 1);
    },
    adjustTextarea(event) {
      const el = event.target;
      autosize(el);
    },
    formValidate() {
      if(this.initials.ru.length === 0 || !this.position.ru.length === 0) {
        this.errorMsg({
          en: 'Not all fields are filled',
          ru: 'Заполнены не все поля',
          by: 'Запоўненыя не ўсе палі',
        });
      } else {
        this.events.forEach((event) => {
          if(event.date.length === 0 ||
          event.title.ru.length === 0 ||
          event.description.ru.length === 0) {
            this.errorMsg({
              en: 'Not all fields are filled',
              ru: 'Заполнены не все поля',
              by: 'Запоўненыя не ўсе палі',
            });
          } else {
            this.alert = false;
            this.alertMessage = {};
          }
        });
      }
    },
    errorMsg(msg) {
      this.alert = true;
      this.alertMessage = msg;
      setTimeout(() => { this.alert = false; }, 5000);
    },
  },
  watch: {
    events() {
      this.alert = false;
    },
  },
};

</script>

<style scoped>
div.alert {
  margin-top: 10px;
  color: #ff7979;
  opacity: 0;
}
.blink {
  animation: BLINK_ANIMATION 5s ease-in-out forwards;
  -webkit-animation: BLINK_ANIMATION 5s ease-in-out forwards;
}
@keyframes BLINK_ANIMATION {
  0%   { opacity: 0; }
  10% { opacity: 1; }
  90% { opacity: 1; }
  100% { opacity: 0; }
}
@-webkit-keyframes BLINK_ANIMATION {
  0%   { opacity: 0; }
  10% { opacity: 1; }
  90% { opacity: 1; }
  100% { opacity: 0; }
}

#events > input {
  border-bottom: none;
}

input[type=text], textarea {
  background: #f9f9f9;
  border: 1px solid #e8e8e8;
}

.event {
  border-top: 3px solid #c7c7c7;
  margin-bottom: 5px;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  padding-top: 10px;
}

.event:first-child {
  border-top: none;
}

input[type="button"].alert {
  background: #ff7979!important;
  box-shadow: inset 0px 1px 0px 0px #ffa8a8!important;
  border: 1px solid #d46363!important;
}

.form-style-8 {
  font-family: 'Open Sans Condensed', arial, sans;
  width: 450px;
  padding: 30px;
  background: #FFFFFF;
  margin: 50px auto;
  box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.22);
  -moz-box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.22);
  -webkit-box-shadow: 0px 0px 15px rgba(0, 0, 0, 0.22);
}

.form-style-8 h2 {
  background: #292c2f;
  text-transform: uppercase;
  font-family: 'Open Sans Condensed', sans-serif;
  color: white;
  font-size: 18px;
  font-weight: 100;
  padding: 20px;
  margin: -30px -30px 30px -30px;
}

.form-style-8 input[type="text"],
.form-style-8 input[type="date"],
.form-style-8 input[type="datetime"],
.form-style-8 input[type="email"],
.form-style-8 input[type="number"],
.form-style-8 input[type="search"],
.form-style-8 input[type="time"],
.form-style-8 input[type="url"],
.form-style-8 input[type="password"],
.form-style-8 textarea,
.form-style-8 select {
  box-sizing: border-box;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  outline: none;
  display: block;
  width: 100%;
  padding: 7px;
  border: none;
  border-bottom: 1px solid #ddd;
  background: transparent;
  margin-bottom: 10px;
  font: 16px Arial, Helvetica, sans-serif;
  height: 45px;
  background: #f9f9f9;
  border: 1px solid #e8e8e8;
}

.form-style-8 *:last-child {
  border-bottom: none;
  margin-bottom: 0;
}

.form-style-8 textarea {
  resize: none;
  overflow: hidden;
}

.form-style-8 input[type="button"],
.form-style-8 input[type="submit"] {
  -moz-box-shadow: inset 0px 1px 0px 0px #45D6D6;
  -webkit-box-shadow: inset 0px 1px 0px 0px #45D6D6;
  box-shadow: inset 0px 1px 0px 0px #45D6D6;
  background-color: #2CBBBB;
  border: 1px solid #27A0A0;
  display: inline-block;
  cursor: pointer;
  color: #FFFFFF;
  font-family: 'Open Sans Condensed', sans-serif;
  font-size: 14px;
  padding: 8px 18px;
  text-decoration: none;
  text-transform: uppercase;
}

.form-style-8 input[type="button"]:hover,
.form-style-8 input[type="submit"]:hover {
  background: linear-gradient(to bottom, #34CACA 5%, #30C9C9 100%);
  background-color: #34CACA;
}

</style>



