<template>
  <timeline :events='people.events' :title='people.title' :position='people.position' ></timeline>
</template>

<script>

import Timeline from '@/components/Timeline';
import peoples from '@/data/peoples';

export default {
  name: 'Peoples',
  components: {
    timeline: Timeline,
  },
  data() {
    return {
      people: peoples[this.$route.path.replace('/', '')].file.default,
    };
  },
};
</script>



