<template>
  <header class="header-basic">
    <div class="header-limiter">
      <h1><router-link class="selected" to="/">THREE<span>PERCENTER</span>BASE</router-link></h1>
      <nav>
        <router-link to="/">{{ home[$i18n.locale] }}</router-link>
        <router-link to='/#peoples'>{{ peoples[$i18n.locale] }}</router-link>
        <router-link to='/#events'>{{ events[$i18n.locale] }}</router-link>
        <router-link to="/add">{{ add[$i18n.locale] }}</router-link>
        <language></language>
      </nav>
    </div>
  </header>
</template>

<script>
import '@/assets/styles/header.css';
import Language from './Language';

export default {
  components: { language: Language },
  data() {
    return {
      home: {
        en: 'Home',
        ru: 'Главная',
        by: 'Галоўная',
      },
      peoples: {
        en: 'Peoples',
        ru: 'Люди',
        by: 'Людзи',
      },
      events: {
        en: 'Events',
        ru: 'События',
        by: 'Падзеі',
      },
      add: {
        en: 'Add',
        ru: 'Добавить',
        by: 'Дабавіць',
      },
    };
  },
};
</script>



