<template>
  <div class="hello">
    <peoples></peoples>
    <h2 name="events">{{ titles.events[$i18n.locale] }}</h2>
    <ul>
      <li>
        <router-link to="/">Home</router-link>
      </li>
    </ul>
  </div>
</template>

<script>
import Peoples from '@/components/Peoples';

export default {
  name: 'Home',
  components: {
    peoples: Peoples,
  },
  data() {
    return {
      titles: {
        events: {
          en: 'Events',
          ru: 'События',
          by: 'Падзеі',
        },
      },
    };
  },
};
</script>

<style scoped>
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
</style>



